import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			id:0,
			user_info:'',
			token:'',
			type:'',
			storetype:''
		}
	},
	onLoad(options) {

		this.id = options.id;
		this.type=options.type;
		
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.storetype = app.globalData.user_info.type;
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	methods: {
		
		//表单提交事件
		formSubmit(e) {
			var data = e.detail.value;
	
			if (data.content == '') {
				wx.showToast({
					title: '请输入拒绝的理由',
					icon: "none"
				});
				return;
			}
	
			var url='';
			var cancel_type='';
			if(this.type==1){
				url = interfaces.examineFail;
			}else{
				url = interfaces.baojiaFail;
				cancel_type=data.cancel_type;
			}
	
			this.request({
				url: url,
				data: {
					content: data.content,
					id: this.id,
					type: this.storetype,
					cancel_type:cancel_type
				},
				header: {
					token:this.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						wx.showToast({
							title: res.data,
							icon: 'none'
						})
						setTimeout(function () {
							wx.reLaunch({
								url: 'index?status=10,11',
							})
						}, 1500);
					} else {
						app.isLogin(res.code, res.data);
					}
				}
			});
		},
	}
}
